.btn {
  color: #00c16c;
  padding: $padding-sm $padding-md;
  border-radius: 32px;
  border: 1px solid #00c16c;
  background: #fff;
  cursor: pointer;
  transition: transform 0.25s ease-in;
  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    background: #00c16c;
    color: white;
  }
}
