@import url("http://fonts.cdnfonts.com/css/museo");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #5e6d82;
}

ul {
  list-style: none;
}

img {
  font-size: 14px;
  font-weight: 700;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.inline-block {
  display: inline-block;
}

body {
  font-family: "Museo", sans-serif;
}

.select {
  position: relative;
  width: 100px;
  margin-bottom: 10px;
  color: #fff;
}
.select__btn {
  padding: 10px;
  width: 100%;
  cursor: pointer;
  position: relative;
  background-color: #333333;
  font-weight: 500;
}
.select__btn::after {
  content: "";
  position: absolute;
  border-right: 2px solid #979696;
  border-bottom: 2px solid #979796;
  width: 6px;
  height: 6px;
  top: 45%;
  transform: rotate(45deg) translateY(-50%);
  right: 20px;
  transition: all 0.25s ease-in;
}
.select__dropdown {
  position: absolute;
  width: 100%;
  background-color: #3d4a5c;
  border-top: 1px solid #eee;
  z-index: 1;
  top: 100%;
  overflow: hidden;
  visibility: hidden;
  transform: scale(1, 0);
  transform-origin: top center;
  transition: all 0.2s ease;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.select__dropdown.toggle {
  visibility: visible;
  transform: scale(1, 1);
}
.select__dropdown .option {
  padding: 10px;
  cursor: pointer;
}
.select__dropdown .option:hover {
  background: #979796;
}

.dropdown {
  display: inline-block;
  width: 50px;
  font-size: 15px;
  font-weight: 500;
  color: #5e6d82;
  position: relative;
}
.dropdown-md {
  width: 150px;
  color: #333;
}
.dropdown button {
  border: transparent;
  outline: none;
  background: #fff;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
  width: 100%;
  cursor: pointer;
  position: relative;
}
.dropdown button::after {
  content: "";
  position: absolute;
  height: 4px;
  width: 4px;
  border-left: 1px solid #5e6d82;
  border-bottom: 1px solid #5e6d82;
  transform: rotate(-45deg);
  top: 5px;
  right: 5px;
}
.dropdown__body {
  position: absolute;
  width: 100%;
  transform: scale(1, 0);
  transform-origin: top center;
  transition: all 0.25s ease-in;
  left: 15px;
  top: 100%;
}
.dropdown__body.open {
  transform: scale(1, 1);
}
.dropdown__body .options {
  padding: 5px 10px;
}
.dropdown__body .options:hover {
  background: #5e6d82;
  color: #fff;
}

.btn {
  color: #00c16c;
  padding: 10px 20px;
  border-radius: 32px;
  border: 1px solid #00c16c;
  background: #fff;
  cursor: pointer;
  transition: transform 0.25s ease-in;
}
.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
  background: #00c16c;
  color: white;
}

.header {
  background-color: #333333;
  min-height: 36px;
  padding-top: 10px;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation {
  box-shadow: 0 2px 4px 0 rgba(222, 222, 222, 0.5);
  position: relative;
  z-index: 10;
}
.navigation .container {
  min-height: 65px;
  display: flex;
  width: 100%;
  align-items: center;
}
.navigation .container .nav__dropdown {
  margin-right: 20px;
}
.navigation .container .coins {
  margin-left: auto;
}
.navigation .container .coins img {
  vertical-align: middle;
}

.nav__dropdown.active .nav__dropdown-list {
  transform: scale(1, 1);
}
.nav__dropdown-btn {
  background: transparent;
  width: 100px;
  cursor: pointer;
  border: none;
  outline: none;
  font-size: 20px;
  color: #3d4a5c;
  position: relative;
  text-transform: uppercase;
  padding: 5px 0;
}
.nav__dropdown-btn::after {
  content: "";
  width: 0;
  height: 0;
  top: 15px;
  right: 10px;
  position: absolute;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid #3d4a5c;
}
.nav__dropdown-list {
  transform: scale(1, 0);
  transform-origin: top center;
  position: absolute;
  transition: transform 0.2s linear;
}
.nav__dropdown-list li {
  padding: 5px 10px;
  text-transform: uppercase;
  color: #3d4a5c;
}

.nav__list {
  width: 40%;
  display: flex;
  justify-content: space-between;
}
.nav__list-link {
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
}

.records__row .records__col {
  width: calc((100% - 150px) / 4);
}
.records__row .records__col:not(:last-child) {
  margin-right: 50px;
}

.row {
  display: flex;
  justify-content: space-between;
  min-height: 75px;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  color: #5e6d82;
  border-bottom: 1px solid #f4f4f4;
}
.row li img {
  vertical-align: middle;
}
.row li a {
  color: #0088cc;
}

.sidebar {
  width: 22vw;
  min-height: 100vh;
  box-shadow: 0 2px 4px 0 rgba(222, 222, 222, 0.5);
  background: #fff;
}

.search {
  padding: 40px 40px 10px 20px;
}
.search__bar {
  border: 1px solid grey;
  padding: 2px 10px;
  border-radius: 32px;
  min-height: 34px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.search__bar-input {
  line-height: 30px;
  outline: none;
  border: transparent;
  width: 85%;
}
.search__bar-input::placeholder {
  color: grey;
}

.sidebar__tabs-list li {
  padding: 0 40px 0 20px;
  width: 100%;
  position: relative;
}
.sidebar__tabs-list li:not(:last-child) {
  margin-bottom: 10px;
}
.sidebar__tabs-list li.active::after {
  content: "";
  position: absolute;
  background: #00aae1;
  width: 3px;
  height: 100%;
  left: 0;
  top: 0;
}
.sidebar__tabs-list li a {
  font-weight: 500;
  color: #303b3d;
  font-size: 14px;
  width: 100%;
  padding: 10px 0;
}

footer {
  display: flex;
  min-height: 80px;
  padding: 0 30px;
  color: #5e6d82;
  font-size: 15px;
  font-weight: 500;
  justify-content: flex-end;
  width: 100%;
  align-items: center;
}
footer .pagination {
  display: flex;
  width: 40%;
  justify-content: space-between;
}
footer .pagination .page-num {
  display: flex;
}
footer .pagination .page-num p {
  display: inline-block;
  margin-right: 10px;
}

.section-content {
  background: #f3f3f3;
  display: flex;
}

.content-wrapper {
  padding: 80px 40px;
  width: calc(100% - 22vw);
}

.content {
  background: #fff;
  border-radius: 10px;
}
.content__head {
  padding: 20px;
}
.content__head-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: #f0ebeb;
  padding: 20px;
  margin-right: 40px;
}
.content__head-img img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  object-fit: cover;
}
.content__head-desc {
  width: calc(100% - 240px);
}
.content__head-desc h1 {
  font-size: 24px;
  margin-top: 10px;
  font-weight: 500;
  color: #333;
  text-transform: capitalize;
}
.content__head-desc p {
  margin-top: 20px;
  color: grey;
  font-weight: 500;
}
.content__head-desc p span {
  color: #63a5ef;
  font-weight: 500;
}
.content__head-desc small {
  display: inline-block;
  margin-top: 10px;
  font-size: 14px;
  color: grey;
  font-weight: 500;
}

.section-chart {
  padding: 0 30px;
}

.title-wrapper {
  padding: 40px 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  color: #5e6d82;
  font-size: 15px;
  font-weight: 500;
  position: relative;
}
.title-wrapper .box {
  position: absolute;
  right: 18%;
  font-size: 14px;
  color: #333;
  box-shadow: 0 2px 4px 0 rgba(222, 222, 222, 0.5);
  top: 25%;
  background: #fff;
  width: 132px;
  height: 115px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 0;
  padding-left: 20px;
}
.title-wrapper .box li {
  position: relative;
}
.title-wrapper .box li::after {
  content: "";
  position: absolute;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  border: 1px solid orange;
  top: 5px;
  right: 105%;
}

.section__tabs {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px solid #e9e9e9;
}
.section__tabs-list {
  width: 40%;
  display: flex;
  justify-content: space-between;
}
.section__tabs-list li {
  padding: 20px 0;
  position: relative;
}
.section__tabs-list li.active::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #63a5ef;
  top: 100%;
  left: 0;
}

.section-stats .grid-row {
  padding: 30px;
}
.section-stats .grid-row article {
  border: 1px solid #f4f4f4;
  text-align: center;
  color: #5e6d82;
  padding: 30px 20px;
  width: calc((100% - 60px) / 3);
}
.section-stats .grid-row article:not(:last-child) {
  margin-right: 30px;
}
.section-stats .grid-row article span {
  font-size: 20px;
}
.section-stats .grid-row article p {
  margin: 20px;
  font-size: 12px;
}

.section-records {
  padding: 30px 30px 20px;
}
.section-records .records__row {
  padding: 20px 120px 20px 0;
  border-bottom: 1px solid #f4f4f4;
}
.section-records .records__row .records__col {
  line-height: 22px;
  color: #5e6d82;
}
.section-records .records__row .records__col img {
  transform: rotate(90deg);
  margin-left: 10px;
  vertical-align: middle;
}

.title__bar {
  min-height: 32px;
  align-items: center;
  display: flex;
  width: 100%;
}
.title__bar-search {
  margin-left: auto;
  border: 1px solid #5e6d82;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 32px;
}
.title__bar-search input {
  border: none;
  outline: none;
  padding-left: 10px;
}
.title__bar-select button {
  border: none;
  background: transparent;
  margin-left: 20px;
}