.navigation {
  box-shadow: 0 2px 4px 0 rgba(222, 222, 222, 0.5);
  position: relative;
  z-index: 10;
  .container {
    min-height: 65px;
    display: flex;
    width: 100%;
    align-items: center;
    .nav__dropdown {
      margin-right: $margin-md;
    }
    .coins {
      margin-left: auto;
      img {
        vertical-align: middle;
      }
    }
  }
}

.nav__dropdown {
  &.active &-list {
    transform: scale(1, 1);
  }
  &-btn {
    background: transparent;
    width: 100px;
    cursor: pointer;
    border: none;
    outline: none;
    font-size: 20px;
    color: $color-secondary-grey;
    position: relative;
    text-transform: uppercase;
    padding: calc($padding-sm/2) 0;

    &::after {
      content: "";
      width: 0;
      height: 0;
      top: 15px;
      right: 10px;
      position: absolute;
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-top: 5px solid $color-secondary-grey;
    }
  }
  &-list {
    transform: scale(1, 0);
    transform-origin: top center;
    position: absolute;
    transition: transform 0.2s linear;

    li {
      padding: 5px $padding-sm;
      text-transform: uppercase;
      color: $color-secondary-grey;
    }
  }
}

.nav__list {
  width: 40%;
  display: flex;
  justify-content: space-between;
  &-link {
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
  }
}
