* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

a {
  display: inline-block;
  text-decoration: none;
  // color: #303b3d;
  color: #5e6d82;
}
ul {
  list-style: none;
}

img {
  font-size: 14px;
  font-weight: 700;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.inline-block {
  display: inline-block;
}
