.sidebar {
  width: 22vw;
  min-height: 100vh;
  box-shadow: 0 2px 4px 0 rgba(222, 222, 222, 0.5);
  background: #fff;
}

.search {
  padding: $padding-lg $padding-lg $padding-sm $padding-md;

  &__bar {
    border: 1px solid grey;
    padding: 2px $padding-sm;
    border-radius: 32px;
    min-height: 34px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-input {
      &::placeholder {
        color: grey;
      }
      line-height: 30px;
      outline: none;
      border: transparent;
      width: 85%;
    }
  }
}

.sidebar__tabs-list {
  li {
    padding: 0 $padding-lg 0 $padding-md;
    width: 100%;
    position: relative;
    &:not(:last-child) {
      margin-bottom: $margin-sm;
    }
    &.active {
      &::after {
        content: "";
        position: absolute;
        background: #00aae1;
        width: 3px;
        height: 100%;
        left: 0;
        top: 0;
      }
    }
    a {
      font-weight: 500;
      color: #303b3d;
      font-size: 14px;
      width: 100%;
      padding: $padding-sm 0;
    }
  }
}
