.section-content {
  background: #f3f3f3;
  display: flex;
}
.content-wrapper {
  padding: calc(2 * $padding-lg) $padding-lg;
  width: calc(100% - 22vw);
}

.content {
  background: #fff;
  border-radius: 10px;

  &__head {
    padding: $padding-md;

    &-img {
      width: 200px;
      height: 200px;
      border-radius: 50%;
      background: rgb(240, 235, 235);
      padding: $padding-md;
      margin-right: $margin-lg;
      img {
        width: 160px;
        height: 160px;
        border-radius: 50%;
        object-fit: cover;
      }
    }
    &-desc {
      width: calc(100% - 240px);
      h1 {
        font-size: 24px;
        margin-top: $margin-sm;
        font-weight: 500;
        color: #333;
        text-transform: capitalize;
      }
      p {
        margin-top: $margin-md;
        color: grey;
        font-weight: 500;
        span {
          color: #63a5ef;
          font-weight: 500;
        }
      }
      small {
        display: inline-block;
        margin-top: $margin-sm;
        font-size: 14px;
        color: grey;
        font-weight: 500;
      }
    }
  }
}

.section-chart {
  padding: 0 $padding-md + 10;
}
.title-wrapper {
  padding: $padding-lg 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  color: #5e6d82;
  font-size: 15px;
  font-weight: 500;
  position: relative;
  .box {
    position: absolute;
    right: 18%;
    font-size: 14px;
    color: #333;
    box-shadow: 0 2px 4px 0 rgba(222, 222, 222, 0.5);
    top: 25%;
    background: #fff;
    width: 132px;
    height: 115px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // align-items: center;
    padding: $padding-md 0;
    padding-left: $padding-md;
    li {
      position: relative;
      &::after {
        content: "";
        position: absolute;
        width: 3px;
        height: 3px;
        border-radius: 50%;
        border: 1px solid orange;
        top: 5px;
        right: 105%;
      }
    }
  }
}
.section__tabs {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px solid #e9e9e9;
  &-list {
    width: 40%;
    display: flex;
    justify-content: space-between;
    li {
      padding: $padding-md 0;
      position: relative;
      &.active::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background: #63a5ef;
        top: 100%;
        left: 0;
      }
    }
  }
}

.section-stats {
  .grid-row {
    padding: 30px;
    article {
      border: 1px solid #f4f4f4;
      text-align: center;
      color: #5e6d82;
      padding: 30px $padding-md;
      width: calc((100% - 60px) / 3);
      &:not(:last-child) {
        margin-right: 30px;
      }
      span {
        font-size: 20px;
      }
      p {
        margin: $margin-md;
        font-size: 12px;
      }
    }
  }
}

.section-records {
  padding: calc(3 * $padding-sm) calc(3 * $padding-sm) $padding-md;
  .records__row {
    padding: $padding-md calc(3 * $padding-lg) $padding-md 0;
    border-bottom: 1px solid #f4f4f4;

    .records__col {
      line-height: 22px;
      color: #5e6d82;
      img {
        transform: rotate(90deg);
        margin-left: $margin-sm;
        vertical-align: middle;
      }
    }
  }
}
.title__bar {
  min-height: 32px;
  align-items: center;
  display: flex;
  width: 100%;
  &-search {
    margin-left: auto;
    border: 1px solid $color-tertiary-grey;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 32px;
    input {
      border: none;
      outline: none;
      padding-left: $padding-sm;
    }
  }
  &-select {
    button {
      border: none;
      background: transparent;
      margin-left: $margin-md;
    }
  }
}
