.records__row {
  .records__col {
    width: calc((100% - 150px) / 4);
    &:not(:last-child) {
      margin-right: 50px;
    }
  }
}
.row {
  display: flex;
  justify-content: space-between;
  min-height: 75px;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  color: #5e6d82;
  border-bottom: 1px solid #f4f4f4;
  li {
    img {
      vertical-align: middle;
    }
    a {
      color: #0088cc;
    }
  }
}
