footer {
  display: flex;
  min-height: 80px;
  padding: 0 30px;
  color: #5e6d82;
  font-size: 15px;
  font-weight: 500;
  justify-content: flex-end;
  width: 100%;
  align-items: center;
  .pagination {
    display: flex;
    width: 40%;
    justify-content: space-between;
    .page-num {
      display: flex;
      p {
        display: inline-block;
        margin-right: $margin-sm;
      }
    }
  }
}
