.select {
  position: relative;
  width: 100px;
  margin-bottom: $margin-sm;
  color: #fff;

  &__btn {
    padding: $padding-sm;
    width: 100%;
    cursor: pointer;
    position: relative;
    background-color: $color-primary-grey;
    font-weight: 500;

    &::after {
      content: "";
      position: absolute;
      border-right: 2px solid rgb(151, 150, 150);
      border-bottom: 2px solid rgb(151, 151, 150);
      width: 6px;
      height: 6px;
      top: 45%;
      transform: rotate(45deg) translateY(-50%);
      right: 20px;
      transition: all 0.25s ease-in;
    }
  }
  &__dropdown {
    position: absolute;
    width: 100%;
    background-color: #3d4a5c;
    border-top: 1px solid #eee;
    z-index: 1;
    top: 100%;
    overflow: hidden;
    visibility: hidden;
    transform: scale(1, 0);
    transform-origin: top center;
    transition: all 0.2s ease;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;

    &.toggle {
      visibility: visible;
      transform: scale(1, 1);
    }

    .option {
      padding: $padding-sm;
      cursor: pointer;
      &:hover {
        background: rgb(151, 151, 150);
      }
    }
  }
}

// pagination dropdown

.dropdown {
  display: inline-block;
  width: 50px;
  font-size: 15px;
  font-weight: 500;
  color: #5e6d82;
  position: relative;
  &-md {
    width: 150px;
    color: #333;
  }
  button {
    border: transparent;
    outline: none;
    background: #fff;
    font-size: inherit;
    font-weight: inherit;
    color: inherit;
    width: 100%;
    cursor: pointer;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      height: 4px;
      width: 4px;
      border-left: 1px solid #5e6d82;
      border-bottom: 1px solid #5e6d82;
      transform: rotate(-45deg);
      top: 5px;
      right: 5px;
    }
  }
  &__body {
    position: absolute;
    width: 100%;
    transform: scale(1, 0);
    transform-origin: top center;
    transition: all 0.25s ease-in;
    left: 15px;
    top: 100%;
    &.open {
      transform: scale(1, 1);
    }

    .options {
      padding: calc($padding-sm/2) $padding-sm;
      &:hover {
        background: #5e6d82;
        color: #fff;
      }
    }
  }
}
